.offer-product-main {
  .offer-product-item {
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
    img{
      height: 260px;
      border-radius: 30px;
      object-fit: cover;
      @media only screen and (max-width: 575px){
        height: 130px;
      }
      @media only screen and (min-width: 576px) and (max-width: 991px){
        height: 200px;
      }
    }
  }
}

.deal-of-the-day-page {
  .del-img{
    img{
      border-radius: 15px;
    }
  }
}
